@media screen and (max-width: 576px) {
  .block--v .blaze-next {
    right: -5% !important;
  }
}
@media screen and (max-width: 580px) {
  .block--v .blaze-next {
    right: -5px !important;
  }
}
.block--v .pre-title {
  text-transform: uppercase;
  font-size: 14px;
  line-height: 24px;
  font-weight: 200;
  letter-spacing: 7px;
}
.block--v .mb-table {
  box-shadow: none;
  position: unset;
}
.block--v .mb-table .mt-box-inner,
.block--v .mb-table .fixed-column-inner {
  background-color: transparent;
}
.block--v .mb-table .mt-box-inner .partner-item,
.block--v .mb-table .fixed-column-inner .partner-item {
  height: 125px;
  justify-content: center;
  align-items: center;
  display: flex;
  padding: 0 !important;
}
.block--v .mb-table .mt-box-inner > div:not(.mt-header):nth-of-type(odd),
.block--v .mb-table .fixed-column-inner > div:not(.mt-header):nth-of-type(odd) {
  background-color: transparent;
}
.block--v .mb-table .mt-box-inner > div:not(.mt-header):nth-of-type(even),
.block--v .mb-table .fixed-column-inner > div:not(.mt-header):nth-of-type(even) {
  background-color: transparent;
}